import Vue from 'vue'
import Router from 'vue-router'
/* import Eco from '@/components/Eco'
import Tickets from '@/components/Tickets'
import About from '@/components/About' */

Vue.use(Router)

export default new Router({
	routes: [
	    {
	      path: '/',
	      component: resolve => require(['../components/Head.vue'], resolve),
		  children: [
		    {
		      path: '/',
		      component: resolve => require(['../components/Eco.vue'], resolve)
		    },
		    {
		      path: '/tickets',
		      component: resolve => require(['../components/Tickets.vue'], resolve)
		    }, {
		      path: '/about',
		      component: resolve => require(['../components/About.vue'], resolve)
		    }, {
		      path: '/owner',
		      component: resolve => require(['../components/Owner.vue'], resolve)
		    }
		  ]
	    },
		
	  ],
	  
/*  routes: [
    {
      path: '/',
      name: 'Eco',
      component: Eco
    },
	{
		path: '/tickets',
		name: 'Tickets',
		component: Tickets
	},
	{
		path: '/about',
		name: 'About',
		component: About
	},
  ] */
})



