	// 授权usdt给门票合约地址 ====>对应Ecoswap网页 "批准",(批准按钮包含两笔交易，1是授权USDT給门票合约，2是调用门票合约的vending接口购买ticketsToken)
	async approveTickets() {
		console.log('开始授权USDT给门票合约');
		// 进行授权
		let addr = this.ticketsAddress;
		let value = this.usdtAppoveQuota;
		let result = await this.gUSDTContract.approve(addr, value).send({
			feeLimit:10000000,
			callValue: 0,
			// true就是20块确认才会给返回值, false就是1个确认就给返回值;
			shouldPollResponse: false
		});
		console.log('授权的返回值', result);
		return result;
	},

	//门票合约接口
	// 投资USDT购买门票接口  ====>对应Ecoswap网页 "批准",批准按钮包含两笔交易，1是授权USDT給门票合约，2是调用门票合约的vending接口购买ticketsToken
	async vending(referrer, value) {
		try {
			let result = await this.gTicketsContract.vending(referrer, value).send({
				feeLimit: 100000000,
				callValue: 0,
				// true就是20块确认才会给返回值, false就是1个确认就给返回值;
				shouldPollResponse: false
			});
			console.log('购买的返回值', result);
			return result;
		} catch (e) {
			console.log("vending error:",e);
		}
	},

	// 激活EID身份,会消耗1000ECO，可根据ECO余额变化判断，激活是否成功
	async activeEid() {// ====>对应Ecoswap网页 "EID 激活"
		try {
			let result = await this.gTicketsContract.activeEid().send({
				feeLimit:10000000,
				callValue: 0,
				// true就是20块确认才会给返回值, false就是1个确认就给返回值;
				shouldPollResponse: false
			});
			console.log('激活EID身份的返回值',result);

			return result;
		} catch(error){
			console.log("activeEid error:",error);
		}

	},

	// 激活EID节点身份,会质押至少3000ECO到门票合约，可根据ECO余额变化判断，激活是否成功
