	// 取现奖金池收益，可根据USDT余额变化判断取现是否成功
	async takePoolIncome() {//====>对应主网页 "日奖池收益"/"周奖池收益"边的"提现"按钮
		try {
			let result = await this.gMasterContract.takePoolIncome().send({
				feeLimit:10000000,
				callValue: 0,
				// true就是20块确认才会给返回值, false就是1个确认就给返回值;
				shouldPollResponse: false
			});
			console.log('取现奖金池收益的返回值',result);

			return result;
		} catch(error){
			console.log("takePoolIncome error:",error);
		}
	},


